:root{
    --anicolor1:rgb(255, 40, 40);
    --anicolor2:rgb(228, 192, 35);
    --anicolor3:rgb(117, 255, 53);
    --anicolor4:rgb(27, 163, 226);
}
.anintro{
    text-align: center;
    padding-top:3%;
    font-size: 50px;
    color: white;
}
.aniback{
    position: fixed;
    background-image: url('https://source.unsplash.com/collection/138471/in-thewild');
    background-size:cover;
    height:100%;
    width: 100%;
    z-index: -10;
}
.animals{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: baseline;
    align-content: space-between;
    width: 80%;
    margin-left:10%;
    padding-top:3vh;
}
.col{
    border-radius: 10px;
    max-height: 300px;
    width: 350px;
    transition: max-height 3s;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    color: white;
    margin:20px;
}
.col p{font-size: 25px; padding-top:5px; padding-bottom:10px;}
.col ul{font-size:18px; margin-left: 20px; margin-top: 5px; padding-bottom:5px; width: 310px;}
.col:hover{max-height:1000px;}
.colChild{text-align: center;}
.colChild img{height: 220px; width:290px;}
.colChild button{font-size:10px; height:20px; width:100px;}
.box1{background-color: var(--anicolor1);}
.box2{background-color: var(--anicolor2);}
.box3{background-color: var(--anicolor3);}
.box4{background-color: var(--anicolor4);}
.box5{background-color: var(--anicolor4);}
.box6{background-color: var(--anicolor1);}
.row a{
    text-decoration: none;
    color: black;
    font-size: 25px;
}